import React, { useState, useEffect } from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import interactionPlugin from '@fullcalendar/interaction';

import './App.css';

//   
const DetailModal = ({ event, onClose }) => {
  if (!event) return null;

  //      
  const getLabel = (category) => {
    switch(category) {
      case '': return ', , ';
      case '': return '';
      case '': return ', ';
      default: return '';
    }
  };

  const getSubLabel = (category) => {
    switch(category) {
      case '': return '';
      case '': return '';
      case '': return '';
      default: return '';
    }
  };

  const mainTitle = event.title.split('\n')[0];
  const creatorLabel = getLabel(event.extendedProps.category);
  const actorsOrAuthorLabel = getSubLabel(event.extendedProps.category);

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <button className="modal-close-btn" onClick={onClose}>&times;</button>
        <div className="modal-header">
          <h2>{mainTitle}</h2>
          <p className="modal-category">{event.extendedProps.category}</p>
        </div>
        <div className="modal-body">
          <div className="modal-section">
            <p><strong>{creatorLabel}:</strong> {event.extendedProps.creator || ''}</p>
            <p><strong>{actorsOrAuthorLabel}:</strong> {event.extendedProps.actorsOrAuthor || ''}</p>
          </div>
          <hr />
          <div className="modal-section review-section">
            <h3> </h3>
            <p>{event.extendedProps.review || ''}</p>
          </div>
        </div>
      </div>
    </div>
  );
};

function App() {
  const [events, setEvents] = useState(() => {
    const saved = localStorage.getItem('movieEvents');
    return saved ? JSON.parse(saved) : [];
  });
  //    
  const [showModal, setShowModal] = useState(false);
  const [selectedEvent, setSelectedEvent] = useState(null);

  // 2 events   
  useEffect(() => {
    localStorage.setItem('movieEvents', JSON.stringify(events));
    console.log(" :", events);
  }, [events]);

  //        
  const handleDateClick = (arg) => {
    const categoryOption = prompt(
      '  ?\n\n1:  \n2:  \n3:  \n\n  ( ESC)'
    );

    if (!categoryOption) return; 

    let title, creator, actorsOrAuthor, review;
    let categoryTitle = ""; 

    switch (categoryOption) {
      case '1': 
        title = prompt('   :');
        if (!title) return;
        creator = prompt(' , ,  ? ( )');
        actorsOrAuthor = prompt('  ? ( )');
        review = prompt('    :');
        categoryTitle = "";
        break;

      case '2': 
        title = prompt('   :');
        if (!title) return;
        creator = prompt('  ?');
        actorsOrAuthor = prompt('  ?');
        review = prompt('    :');
        categoryTitle = "";
        break;

      case '3': 
        title = prompt('   :');
        if (!title) return;
        creator = prompt('  ?');
        actorsOrAuthor = prompt('  ?');
        review = prompt('    :');
        categoryTitle = "";
        break;

      default:
        alert('  .  .');
        return;
    }

    const fullTitle = `${title}
${categoryTitle === "" ? " " : categoryTitle === "" ? " " : " , "}: ${creator || ""}
${categoryTitle === "" ? " " : categoryTitle === "" ? " " : " "}: ${actorsOrAuthor || ""}
 : ${review || ""}`;

    setEvents([
      ...events,
      {
        title: fullTitle,
        date: arg.dateStr,
        extendedProps: {
          category: categoryTitle, 
          creator,
          actorsOrAuthor,
          review,
        },
      },
    ]);
  };

  //      
  const handleEventClick = (clickInfo) => {
    const event = clickInfo.event;
    //      ,   /  
    setSelectedEvent(event);
    setShowModal(true);
  };
  
  //  
  const handleCloseModal = () => {
    setShowModal(false);
    setSelectedEvent(null);
  };
  
  //   
  const handleEdit = () => {
    const currentCategory = selectedEvent.extendedProps?.category;
    const currentTitle = selectedEvent.title.split("\n")[0].trim();
    let updatedCreator, updatedActorsOrAuthor, updatedReview;
    let promptCreator, promptActorsOrAuthor;
  
    if (currentCategory === "") {
      promptCreator = ' , ,  :';
      promptActorsOrAuthor = '  :';
    } else if (currentCategory === "") {
      promptCreator = '  :';
      promptActorsOrAuthor = '  :';
    } else { // 
      promptCreator = ' ,  :';
      promptActorsOrAuthor = '  :';
    }
  
    updatedCreator = prompt(promptCreator, selectedEvent.extendedProps?.creator || "");
    updatedActorsOrAuthor = prompt(promptActorsOrAuthor, selectedEvent.extendedProps?.actorsOrAuthor || "");
    updatedReview = prompt('  :', selectedEvent.extendedProps?.review || "");

    if (!updatedCreator && !updatedActorsOrAuthor && !updatedReview) return;
  
    const newTitle = `${currentTitle}
${currentCategory === "" ? " " : currentCategory === "" ? " " : " , "}: ${updatedCreator || ""}
${currentCategory === "" ? " " : currentCategory === "" ? " " : " "}: ${updatedActorsOrAuthor || ""}
 : ${updatedReview || ""}`;
  
    setEvents((prevEvents) =>
      prevEvents.map((ev) =>
        ev.date === selectedEvent.startStr && ev.title === selectedEvent.title
          ? {
              ...ev,
              title: newTitle,
              extendedProps: {
                category: currentCategory,
                creator: updatedCreator,
                actorsOrAuthor: updatedActorsOrAuthor,
                review: updatedReview,
              },
            }
          : ev
      )
    );
    handleCloseModal(); //    
  };
  
  //   
  const handleDelete = () => {
    if (window.confirm(" ? ")) {
      setEvents((prevEvents) =>
        prevEvents.filter(
          (ev) => !(ev.date === selectedEvent.startStr && ev.title === selectedEvent.title)
        )
      );
      handleCloseModal(); //    
    }
  };
  

  return (
    <div className="App">
      <h1> ferarchive</h1>
      <FullCalendar
        plugins={[dayGridPlugin, interactionPlugin]}
        initialView="dayGridMonth"
        events={events}
        dateClick={handleDateClick}
        eventClick={handleEventClick}
        eventContent={(arg) => {
          return { html: arg.event.title.replace(/\n/g, "<br/>") };
        }}
        height="auto"
      />
      {showModal && (
        <DetailModal
          event={selectedEvent}
          onClose={handleCloseModal}
        />
      )}
    </div>
  );
}

export default App;